*, :after, :before {
  box-sizing: inherit;
}

:root {
  --backgroundColor: #222;
  --color: white;
  --inputColor: var(--backgroundColor);
  box-sizing: border-box;
}

@media (prefers-color-scheme: light) {
  :root {
    --backgroundColor: white;
    --color: #222;
    --inputColor: var(--color);
  }
}

body {
  background-color: var(--backgroundColor);
  color: var(--color);
  font-family: sans-serif;
}

.section {
  padding-top: 30px;
}

.form-label {
  width: 100%;
}

.form-control {
  color: var(--inputColor);
  font-family: inherit;
  font-size: 1.3em;
}

.form {
  max-width: 400px;
  flex-direction: column;
  gap: 1em;
  padding: 2.5em;
  display: flex;
}

.form-output {
  width: 100%;
  font-size: 1.2em;
}

.info__item {
  border: 1px solid var(--color);
  padding: 10px;
}

.info__item:not(:last-of-type) {
  border-bottom: none;
}

.white-space-nowrap {
  white-space: nowrap;
}

.table-wrapper {
  --borderWidth: 2px;
  border: var(--borderWidth) solid var(--color);
  white-space: nowrap;
  margin-top: 50px;
  overflow-y: hidden;
}

.table {
  color: var(--color);
  width: 100%;
  margin-bottom: 0;
  display: table;
}

.table tr th, .table tr td {
  text-align: center;
  vertical-align: middle;
}

.table tr td:not(:last-child), .table tr th:not(:last-child) {
  border-right: var(--borderWidth) solid var(--color);
}

.table tbody tr:not(:last-child), .table thead tr {
  border-bottom: var(--borderWidth) solid var(--color);
}

@media (max-width: 400px) {
  .table {
    font-size: .6em;
  }
}

@media (max-width: 768px) {
  .table {
    font-size: .65em;
  }
}

